;\r\n}\r\n\r\nh1 {\r\n  line-height: 1;\r\n}\r\n\r\nbutton {\r\n  cursor: pointer;\r\n}\r\n\r\n/* Remove list styles on ul, ol elements with a list role, which suggests default styling will be removed */\r\nul[role='list'],\r\nol[role='list'] {\r\n  list-style: none;\r\n}\r\n\r\n/* Set core root defaults */\r\nhtml:focus-within {\r\n  scroll-behavior: smooth;\r\n}\r\n\r\n/* Set core body defaults */\r\nbody {\r\n  min-height: 100vh;\r\n  text-rendering: optimizeSpeed;\r\n  line-height: 1.5;\r\n}\r\n\r\n/* A elements that don't have a class get default styles */\r\na:not([class]) {\r\n  text-decoration-skip-ink: auto;\r\n}\r\n\r\n/* Make images easier to work with */\r\nimg,\r\npicture {\r\n  max-width: 100%;\r\n  display: block;\r\n}\r\n\r\n/* Inherit fonts for inputs and buttons */\r\ninput,\r\nbutton,\r\ntextarea,\r\nselect {\r\n  font: inherit;\r\n}\r\n\r\n/* Visually hides content but screen reader can read it */\r\n.sr-only {\r\n  position:absolute;\r\n  left:-10000px;\r\n  top:auto;\r\n  width:1px;\r\n  height:1px;\r\n  overflow:hidden;\r\n  }\r\n\r\n/* Remove all animations, transitions and smooth scroll for people that prefer not to see them */\r\n@media (prefers-reduced-motion: reduce) {\r\n  html:focus-within {\r\n   scroll-behavior: auto;\r\n  }\r\n  \r\n  *,\r\n  *::before,\r\n  *::after {\r\n    animation-duration: 0.01ms !important;\r\n    animation-iteration-count: 1 !important;\r\n    transition-duration: 0.01ms !important;\r\n    scroll-behavior: auto !important;\r\n  }\r\n}\r\n","@use \"variables\";\r\n\r\n.flex-row {\r\n    display: flex;\r\n  }\r\n  \r\n.flex-col {\r\n  display: flex;\r\n  flex-direction: column;\r\n}\r\n\r\n.text-left {\r\n  text-align: left;\r\n}\r\n\r\n.text-right {\r\n  text-align: right;\r\n}\r\n\r\n.text-center {\r\n  text-align: center;\r\n}\r\n\r\n.text-contrast {\r\n  color: variables.$clr-contrast;\r\n}\r\n\r\n.text-dark {\r\n  color: variables.$clr-darkest;\r\n}\r\n\r\n.text-light {\r\n  color: variables.$clr-light;\r\n}\r\n\r\n.fs-900 {\r\n  font-size: variables.$fs-900;\r\n}\r\n\r\n.fs-800 {\r\n  font-size: variables.$fs-800;\r\n}\r\n\r\n.fs-700 {\r\n  font-size: variables.$fs-700;\r\n}\r\n\r\n.fs-600 {\r\n  font-size: variables.$fs-600;\r\n}\r\n\r\n.fs-500 {\r\n  font-size: variables.$fs-500;\r\n}\r\n\r\n.fs-400 {\r\n  font-size: variables.$fs-400;\r\n}\r\n\r\n.fs-300 {\r\n  font-size: variables.$fs-300;\r\n}\r\n\r\n.fs-200 {\r\n  font-size: variables.$fs-200;\r\n}\r\n\r\n.ff-headers {\r\n  font-family: variables.$ff-headers;\r\n}\r\n\r\n.disable-scroll {\r\n  overflow: hidden;\r\n}","\r\n/* colors */\r\n$clr-darkest: rgb(41, 40, 95);\r\n$clr-contrast: rgb(65, 160, 70);\r\n$clr-dark: rgb(107, 107, 107);\r\n$clr-less-dark: rgba(200,200,200,1);\r\n$clr-light: rgb(248, 247, 247);\r\n$clr-very-light: rgba(255,255,255,1);\r\n\r\n/* font sizes */\r\n$fs-900: 8rem;\r\n$fs-800: 5.5rem;\r\n$fs-700: 4rem;\r\n$fs-600: 3rem;\r\n$fs-500: 2rem;\r\n$fs-400: 1.5rem;\r\n$fs-300: 1.25rem;\r\n$fs-200: 0.875rem;\r\n\r\n/* font-families */\r\n$ff-headers: 'Dongle', sans-serif;\r\n\r\n/* screen-sizes */\r\n$screen-small: \"50rem\";","@use 'variables';\r\n// styles for radio buttons on day choose in daily summary\r\n\r\n#choose-day {\r\n    margin-bottom: 3rem;\r\n    width: 100%;\r\n    display: flex;\r\n    justify-content: center;\r\n    gap: 1rem;\r\n\r\n    @media (max-width: variables.$screen-small) {\r\n        margin: 0;\r\n        margin-top: 1rem;\r\n        flex-wrap: wrap;\r\n    }\r\n\r\n    .single-day-date {\r\n        height: 100%;\r\n        width: fit-content;\r\n        cursor: pointer;\r\n        padding: 0.2rem 0.5rem;\r\n        color: variables.$clr-darkest;\r\n        font-size: variables.$fs-400;\r\n        border: 2px solid variables.$clr-darkest;\r\n        border-radius: 5rem;\r\n\r\n        @media (max-width: variables.$screen-small) {\r\n            padding: 0.1rem 0.3rem;\r\n            font-size: variables.$fs-300;\r\n        }\r\n\r\n        &[aria-selected=true] {\r